import { create } from "zustand";
import { Post } from "./model/Post";
import { persist, createJSONStorage } from 'zustand/middleware';

interface PostState {
  Posts: any[];
  ScrabSlice: any[];
  Tab: Boolean;
  Popup: Boolean;
  setTab: (data: Boolean) => void;
  setPopup: (data: Boolean) => void;
  setPostData: (data: any[]) => void;
  resetPostData: (data: any[]) => void;
  setScrab: (data: any[]) => void;
  setScrabSlice: (data: any[]) => void;
  hHeadLine: string;
  hDate: Date | null;
  hCountry: any[];
  sHeadLine: string;
  sDate: Date | null;
  sCountry: any[];
  setHeadLine: (data: string) => void;
  setDate: (data: Date | null) => void;
  setCountry: (data: any[]) => void;
  hPage: number;
  sPage: number;
  setPage: (data: number) => void;
  setPageUp: () => void;
}
export const useStore = create<PostState>((set) => ({
  Posts: [],
  ScrabSlice: [],
  Tab: true,
  Popup: false,
  hHeadLine: "",
  hDate: null,
  hCountry: [],
  sHeadLine: "",
  sDate: null,
  sCountry: [],
  hPage: 1,
  sPage: 1,
  setTab: (data) => {
    set(() => ({
      Tab: data
    }))
  },
  setPopup: (data) => {
    set(() => ({
      Popup: data
    }))
  },
  setPostData: (data) => {
    set((state) => ({
      Posts: [...state.Posts, ...data]
    }));
  },
  resetPostData: (data) => { //  
    set((state) => ({
      Posts: [...data]
    }));
  },
  setScrab: (data) => { //  6 
    set((state) => ({
      ScrabSlice: [...data]
    }));
  },
  setScrabSlice: (data) => { //  6 
    set((state) => ({
      ScrabSlice: [...state.ScrabSlice, ...data]
    }));
  },
  setHeadLine: (data) => {
    set((state) => {
      if (state.Tab) {
        return { hHeadLine: data }; // 
      } else {
        return { sHeadLine: data }; // 
      }
    });
  },
  setDate: (data) => {
    set((state) => {
      if (state.Tab) {
        return { hDate: data }; // 
      } else {
        return { sDate: data }; // 
      }
    });
  },
  setCountry: (data) => {
    set((state) => {
      if (state.Tab) {
        return { hCountry: data }; // 
      } else {
        return { sCountry: data }; // 
      }
    });
  },
  setPage: (data) => {
    set((state) => {
      if (state.Tab) {
        return { hPage: data }; // 
      } else {
        return { sPage: data }; // 
      }
    });
  },
  setPageUp: () => {
    set((state) => {
      if (state.Tab) {
        return { hPage: state.hPage + 1 }; // 
      } else {
        return { sPage: state.sPage + 1 }; // 
      }
    });
  },
}));

interface PersistState {
  Scrabs: any[];
  setScrabsData: (data: any) => void;
}
export const useStorePersist = create<PersistState>()(
  persist(
    (set, get) => ({
      Scrabs: [],
      setScrabsData: (data: any) => {
        const isInArray = get().Scrabs.some(scrab => scrab._id === data._id);
        if(isInArray){ //  
          set((state: { Scrabs: any; }) => ({
            Scrabs: state.Scrabs.filter((scrab: { _id: any; }) => scrab._id !== data._id),
          }));
        } else {    // 
          set((state: { Scrabs: any; }) => ({
            Scrabs: [...state.Scrabs, data],
          }));
        }
      },
    }),
    {
      name: 'nyt-storage', // name of the item in the storage (must be unique)
      storage: createJSONStorage(() => localStorage), // (optional) by default, 'localStorage' is used
      partialize: (state) => ({ Scrabs: state.Scrabs }),
    }
  )
)

